import axios from 'axios';
import http from '../plugins/http';

class WebClientSpw {

  constructor(idregional) {
    this.endPoints = {};

    //Chave
    switch (idregional) {
      case 1:
        this.chave = '14facc52-dd80-4e60-b8e5-dbd1636e20ee';
        this.endPoints.SituacaoCadastralFinanceira = 'https://servico.creci-rj.gov.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira';
        break;
      case 4:
        this.chave = '1fc975e0-424b-4601-9edc-25c6e8b7d81b';
        this.endPoints.SituacaoCadastralFinanceira = 'https://crecimg2.spiderware.com.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira';
        break;
      default:
        this.chave = null;
        throw new Error('WebClientSpw: Regional inválido');
    }
  }

  //Obtem informação financeira atual do corretor. Null = CPF não encontrado
  async getSituacaoCadastralFinanceira(cpf) {
    const {data: response} = await axios.get(this.endPoints.SituacaoCadastralFinanceira, {
      headers: {
        chave: this.chave,
        cpf_cnpj: cpf,
        cadastroCompleto: 'NAO',
      }
    });
    if (!response || (Array.isArray(response) && response.length === 0)) return null;
    return response[0];
  }

  //Atualiza a informação financeira do corretor no banco do COFECI.
  async updateSituacaoCadastralFinanceira(SituacaoCadastralFinanceira, tokenCofeci, silent = false, homologacao = false) {
    const dataValidadePadrao = ((new Date()).getFullYear() + 1) + '-04-15'; //Á vista a SPW nao informa a data.

    //Formatação de dados para o JSON
    let idSituacao = undefined;
    let datavalidade = undefined;
    let debitoCreci = 'S';
    if (SituacaoCadastralFinanceira.situacaoFinanceira === "EM DIA") {
      idSituacao = SituacaoCadastralFinanceira.dataValidade ? 2 : 1;
      datavalidade = SituacaoCadastralFinanceira.dataValidade ? SituacaoCadastralFinanceira.dataValidade.substr(6,4) + "-" + SituacaoCadastralFinanceira.dataValidade.substr(3,2) + "-" + SituacaoCadastralFinanceira.dataValidade.substr(0,2) : dataValidadePadrao;
      debitoCreci = SituacaoCadastralFinanceira.dataValidade ? "S" : "N";
    }

    //Corpo JSON da requisição
    const jsonBody = {
      'Token': tokenCofeci,
      'CodigoInscricao': 'numero de creci aqui em INT',
      'IdCategoriaInscricao': 1,
      'Pessoa': 'F',
      'IdSituacao': idSituacao,
      'DataValidade': datavalidade,
      'DebitoCreci': debitoCreci,
      'homologacao': homologacao ? true : undefined
    };

    //Envio
    if (silent) await axios.post(http.cofeciApi.defaults.baseURL + '/validadedocumento.php', JSON.stringify(jsonBody));
    else await http.cofeciApi.post('/validadedocumento.php', JSON.stringify(jsonBody));
  }
}

export default WebClientSpw;